import { service as request } from 'util/request'

// sku    GET /admin/product/list/{page}/{limit}
export const skuList = ({ page, limit }) => {
  return request({
    url: `/admin/product/list/${page}/${limit}`,
    method: 'get'
  })
}

//  GET /admin/product/onSale/{skuId}
export const onSale = (skuId) => {
  return request({
    url: `/admin/product/onSale/${skuId}`,
    method: 'get'
  })
}

//  GET /admin/product/cancelSale/{skuId}
export const cancelSale = (skuId) => {
  return request({
    url: `/admin/product/cancelSale/${skuId}`,
    method: 'get'
  })
}

//  sku   /admin/product/getSkuById/{skuId}
export const getSkuById = (skuId) => {
  return request({
    url: `/admin/product/getSkuById/${skuId}`,
    method: 'get'
  })
}

//  sku   DELETE /admin/product/deleteSku/{skuId}
export const deleteSku = (skuId) => {
  return request({
    url: `/admin/product/deleteSku/${skuId}`,
    method: 'delete'
  })
}
